<template>
    <div>
       <router-link 
        v-if="setSidebar(inte.children)"
        class="sidebar_lia"
        tag="li"
        :to="{path: inte.path}" 
    >
    <div>
        <span>{{ title[this.onlyOneChild.meta.title] }}</span>
    </div>
        <!-- <div class="sidebar_lia_hide"  :data-arrow="!!inte.list"   @click.self="onUnfoid($event)">
            <div 
                :class="[tier === 0? 'sidbar_title': 'sidebar_secondary']" 
                :style="paddinLeft"  
            >
                
            </div> 
            <content-list :tier="addTier" v-if="inte.list" :lists="inte.list"></content-list>   
        </div> -->
              
    </router-link>
    <router-link v-else :to="{path: inte.path}"  ></router-link> 
    </div>
    
</template>

<script>
import { mapState  } from 'vuex'

export default {
	name: 'content-list',
	props: {
        tier: {
            type: Number,
            default: 0
        },
        inte: {
            type: Object,
            default: {}
        }
	},
    data: function() {
        return {
            onlyOneChild: null
        }
    },
    computed: {
        ...mapState([
            'title'
        ])
        // addTier: function() {
        //     var tier = this.tier;
        //     return  ++tier;
        // },
        // paddinLeft: function() {
        //     return {
        //         'padding-left': this.tier * 20 + 20 + 'px'  
        //     } 
        // },
    },
    methods: {
        // 设置侧边栏
        setSidebar: function(children) {
            if(children.length === 1) {
                this.onlyOneChild = children[0] 
                console.log( this.onlyOneChild.meta.title)
                return true
            }  

            return false
        }
        // onUnfoid: function(vm) {
        //     if(vm.target.getAttribute("data-arrow")) {  //,"arrow_bottom", "arrow"
        //         console.log(1)
        //         vm.target.classList.toggle("sidebar_lia_show");
        //     }
        // },
        // activeClass: function(inte) {
        //     if(inte) {
        //         return ''
        //     } else {
        //         return 'is_active'
        //     }
        // }
    },

}
</script>




