import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


var store = new Vuex.Store({
	state: {
        title: {
            homePage: '首页',
            documents: '文档',
            introduction: '介绍',
            nested: '菜单',
            munt1: '菜单1',
            munt2: '菜单2',
            munt3: '菜单3',
            munt4: '菜单4',
            'munt2-1': '菜单2-1',
            'munt2-2': '菜单2-2',
            'munt2-3': '菜单2-3',
            'munt2-2-1': '菜单2-2-1',
            'munt2-2-2': '菜单2-2-2',
            'munt2-2-3': '菜单2-2-3',
        }    
	},
    getters: {
        num2: function(state, num) {
            console.log(state, num);
            return function(id) {
                return id
            }
        },
        str: function() {
            return 'abc'
        }
    },
	mutations: {
		addCount: function(state, num) {
			state.count += num
		}
	}
})

export default store




